@use 'base/variables' as v;
@use 'base/mixins' as m;

.planes{
    background-image: url(/build/img/PastorAleman.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    //background-size: 2600px;
   /* @include m.telefono{
        background-size: 2500px;
    }*/
    @include m.tablet{
        background-size: 1280px;
        background-position: top center;
    }
    @include m.desktop{
        background-size: contain;
    }
    &__contenedor{
        @include m.contenedor;

    }
    &__titulo{
        padding: 3rem 0;
        h2{
            margin: 0;
        }
        .tituloblanco{
                color: v.$blanco;
        }
        .tituloverde{
                color: v.$verde;
        }
        @include m.tablet{
            display: flex;
            justify-content: right;
            .detalletitulo{
                width: 40%;
                .tituloverde, .tituloblanco{
                    text-align: start;
                }
            }
        } 
        @include m.tabletxl{
            .detalletitulo{
                width: 25%;
            }
        }   
    }
    &__planescontenido{
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet{
            flex-direction: row;
            justify-content: flex-end;

        }
    }
    &__plan{
        position: relative;
        display: inline-block;
        //width: 30rem;
        //margin: 0 auto;
        //align-self: center;
    }
    &__imghueso{
        width: 30rem;
    }
    &__plancontenido{
        width: 20rem;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    &__precio{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 2rem;
        img{
            width: 5rem;
            height: 4rem;
        }
    }
    &__moneda{
        line-height: 1;
        .divisa{
            text-align: right;
        }
        .valor{
            font-size: 4rem;
            font-weight: 700;
        }
    }
    &__detalleplan{
        margin-top: 4rem;
        padding: 0 3rem;
    }
    &__nombreplan{
        margin-bottom: 1rem;
    }
    &__descripcion{
        color: v.$gris;
        font-size: 1.2rem;
        font-weight: 300;
    }
    &__btn{
        a{
            @include m.boton(v.$amarillo, v.$oscuro);
            font-size: 1rem;
        }
        text-align: end;
    }
}