@use 'base/variables' as v;
@use 'base/mixins' as m;

.line-seudoelement{
    &--up{
        &::before{  //creacion de seudo elemento, pinta linea horizontal verde antes del h2 ="cursos-populares" 
            content: ''; 
            width: 4rem;
            height: 0.4rem;
            display: block;
            background-color: v.$verde;
            margin: auto;
            margin-bottom: 1rem;  
        }
    }
    &--down{
        &::after{  //creacion de seudo elemento, pinta linea horizontal verde antes del h2 ="cursos-populares" 
            content: ''; 
            width: 4rem;
            height: 0.4rem;
            display: block;
            background-color: v.$verde;
            margin: auto;
            margin-top: 1rem;  
        }
    }
}

.boton-arriba{
    width: 4rem;
    height: 4rem;
    background-color: v.$gristraparente;
    border-radius: 50%;
    position: fixed;
    bottom: 13rem;
    right: 2.8rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    //display: none;
    visibility: hidden;
    z-index: 8;
    transition: background .5s ease;
    &:hover {
        background-color: v.$gris;
    }
    .btn-up {
        color: v.$blanco;
        font-size: 2rem;
        font-weight: 400;
        padding: 1rem;
    }
}
