@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    &__contenedor{
        @include m.contenedor;
        padding: 2rem 1rem;
        @include m.tablet{
            display: flex;
            justify-content: space-around;
            .footer__infoempresa{
                width: 30rem;
            }
        }
        .colum{
            padding: 1rem 2rem;
            font-size: 1.3rem;
            font-weight: 300;
            line-height: 1.5;
            color: v.$gris;
            a{
                display: block;
                color: v.$gris;
            }
        }
    }
    &__logopetsanar{
        width: 12rem;
        margin-bottom: 3rem;
    }

    &__infoempresa{
        
    }
    &__navegacion{
      
    }
    &__mas{
     
    }
}