body {
  font-family: "Sofia Pro", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  font-family: "Sofia Pro", sans-serif;
  text-decoration: none;
}

p {
  font-family: "Sofia Pro", sans-serif;
}

img {
  max-width: 100%;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  font-family: "Simply Rounded", sans-serif;
  margin: 0 0 2.5rem 0;
  color: #000000;
  text-align: center;
}

h1 {
  font-size: 3.5rem;
  font-weight: 900;
}

h2 {
  font-size: 2.9rem;
  font-weight: 700;
}

h3 {
  font-size: 2.1rem;
}

h4 {
  font-family: "Simply Rounded", sans-serif;
  font-size: 1.7rem;
  margin: 0;
}

p {
  font-size: 1.4rem;
  margin: 0;
}

.line-seudoelement--up::before {
  content: "";
  width: 4rem;
  height: 0.4rem;
  display: block;
  background-color: #69B884;
  margin: auto;
  margin-bottom: 1rem;
}
.line-seudoelement--down::after {
  content: "";
  width: 4rem;
  height: 0.4rem;
  display: block;
  background-color: #69B884;
  margin: auto;
  margin-top: 1rem;
}

.boton-arriba {
  width: 4rem;
  height: 4rem;
  background-color: rgba(204, 204, 223, 0.4);
  border-radius: 50%;
  position: fixed;
  bottom: 13rem;
  right: 2.8rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  z-index: 8;
  transition: background 0.5s ease;
}
.boton-arriba:hover {
  background-color: #9191A6;
}
.boton-arriba .btn-up {
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 400;
  padding: 1rem;
}

.header {
  position: relative;
  min-height: 40rem;
  background-color: #183838;
  background-image: url(/build/img/PERRITO1.png), linear-gradient(270deg, rgba(68, 68, 68, 0.1), rgba(42, 42, 42, 0.295)), url(/build/img/circulos-semiblanco-izq.svg), url(/build/img/circulos-blancos-der.svg);
  background-repeat: no-repeat;
  background-size: contain, cover, 440px, 440px;
  background-position: center bottom, center center, 50% 500%, left 130%;
}
@media (min-width: 480px) {
  .header {
    min-height: 50rem;
    background-size: contain, cover, 500px, 500px;
    background-position: right bottom, center center, -20% 250%, 140% 50%;
  }
}
@media (width: 480px) {
  .header {
    min-height: 50rem;
    background-size: contain, cover, 460px, 450px;
    background-position: right bottom, center center, 0% 250%, 100% 50%;
  }
}
@media (min-width: 768px) {
  .header {
    min-height: 60rem;
    background-size: contain, cover, 600px, 550px;
    background-position: right bottom, center center, -10% 330%, 112% -20%;
  }
}
.header__contenedor {
  position: relative;
  width: min(95%, 144rem);
  margin: 0 auto;
  min-height: 40rem;
}
@media (min-width: 480px) {
  .header__contenedor {
    min-height: 50rem;
  }
}
@media (min-width: 768px) {
  .header__contenedor {
    min-height: 60rem;
  }
}
.header__barranav {
  padding: 2rem 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
@media (min-width: 992px) {
  .header__barranav {
    width: 50%;
  }
}
.header__logo {
  width: 15rem;
}
.header__nav {
  display: none;
}
.header__nav a {
  font-size: 1.4rem;
  color: #FFFFFF;
  border-left: 1px solid #9191A6;
  padding: 0.3rem 1rem;
  transition: all 0.5s ease;
}
.header__nav a:last-of-type {
  border-right: 1px solid #9191A6;
}
.header__nav a:hover {
  color: #69B884;
}
@media (min-width: 992px) {
  .header__nav {
    display: flex;
  }
}
@media (min-width: 992px) {
  .header__btnmenumovil {
    display: none;
  }
}
.header__btnmenumovil .bars {
  color: #FFFFFF;
  font-size: 3rem;
  cursor: pointer;
}
.header__contenidotext {
  text-align: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
@media (min-width: 480px) {
  .header__contenidotext {
    text-align: left;
  }
}
.header__titulo {
  font-size: clamp(2.8rem, -0.8rem + 10vw, 5rem);
  color: #FFFFFF;
}
@media (min-width: 480px) {
  .header__titulo {
    text-align: left;
  }
}
.header__titulo span {
  color: #69B884;
}
.header__descripcion {
  color: #EBEBEB;
}
.header__enlace {
  background-color: #69B884;
  padding: 1.5rem 3rem;
  color: #FFFFFF;
  font-size: 1.3rem;
  font-weight: 300;
  display: inline-block;
  border: none;
  border-radius: 3rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  margin: 1rem 0;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .header__enlace {
    width: auto;
  }
}
.header__enlace:hover {
  background-color: #4ca26a;
  cursor: pointer;
}
.header__menumovil {
  position: absolute;
  top: 0%;
  left: 0%;
  width: 0rem;
  height: 100%;
  background-color: rgba(105, 184, 132, 0.98);
  overflow: hidden;
  transition: all 0.6s ease;
}
.header .menumovil-js {
  width: 20rem;
}
.header__navmovil {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 1rem;
  padding: 0 0 0 3rem;
}
.header__navmovil .logomenumovil {
  width: 12rem;
  margin-bottom: 2rem;
}
.header__navmovil a {
  font-size: 1.4rem;
  padding: 1rem 0;
  color: #EBEBEB;
  position: relative;
}
.header__navmovil a::after {
  content: "";
  height: 1px;
  width: 0;
  background-color: #FFFFFF;
  display: block;
  position: absolute;
  left: 50%;
  bottom: 0;
  transition: all 0.5s ease;
}
.header__navmovil a:hover::after {
  width: 100%;
  left: 0;
}
.header__navmovil a:hover {
  color: #FFFFFF;
}

.petsanar {
