.veterinarias {
  background: linear-gradient(360deg, #BDE6CB, #E4F2E9);
  border-bottom: 4px solid #183838;
}
.veterinarias__contenedor {
  width: min(95%, 144rem);
  margin: 0 auto;
  position: relative;
  padding: 7rem 0;
}
.veterinarias__titulo {
  color: #183838;
}
.veterinarias__vinculadas {
  margin-top: 4rem;
  margin-bottom: 1rem;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 480px) {
  .veterinarias__vinculadas {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 3rem;
  }
