@use 'base/variables' as v;
@use 'base/mixins' as m;

.novedades{
    background-color: v.$grisclaro;
    padding: 3rem 0;
    background-image: url(/build/img/huella.png);
    background-repeat: no-repeat;
    background-size: 150px;
    background-position: right top;
    &__contenedor{
        @include m.contenedor;
        position: relative;
    }
    &__contenido{
        position: relative;
        z-index: 1;
    }
    &__titulo{
        color: v.$verdemilitaroscuro;
        margin: 0;
    }
    &__tituloverde{
        color: v.$verde;
    }
    &__subcontenido{
        @include m.tabletxl{
        padding-left: 6rem;
        }
    }
    &__titulofarmacia{
        @include m.tablet{
            text-align: left;
        }
        white-space: pre;
        img{
            width: 3.3rem;
        }
    }
    &__parrafo{
        color: v.$verdemilitaroscuro;
        text-align: center;
        @include m.tablet{
            text-align: left;
        }
    }

    &__medicamentos{
        margin-top: 3rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;
        @include m.tablet{
           display: inline-flex;
           flex-direction: row;
           align-items: unset;
        }
    }
    &__medicamento{
        background-color: v.$blanco;
        color: v.$verdemilitaroscuro;
        border-radius: 1rem;
        width: 24rem;
        padding: 3rem;
        position: relative;
        .line-seudoelement--down{
            &::after{
                margin-left: 0 !important;
            }
        }
        .tipo-medicamento{
            
        }
        .observacion{

        }
        .lista{
            list-style: inside;
            padding: 1rem 0;
            li{
                font-size: 1.4rem;
                &::marker{  //viñeta de li
                    color: v.$verde;
                    font-size: 2rem;
                }
            }
        }
        .super-p-verde{
            position: absolute;
            z-index: -1;
            width: 6.5rem;
            @include m.telefono{
                width: 8rem;
            }
            @include m.tabletxl{
                width: 9rem;
            }
            top: 0%;
            left: 95%;
            transform: translateY(-50%); 
        }
    }

    //////////banner1.png////////////
    &__img{ 
        text-align: center;
        @include m.telefono{
            position: absolute;
            bottom: 40%;
            right: 0;
        }
        @include m.tablet{
            position: absolute;
            top: 0;
            right: 0;
        }
        
        .banner{
            width: 20rem;
            @include m.telefono{
                width: 22rem;
            }
            @include m.tablet{
                width: 45rem;
            }
            @include m.tabletl{
                width: 48rem;
            }
            @include m.tabletxl{
                width: 51rem;
            }
        }
    }
    
}