@use 'base/variables' as v;
@use 'base/mixins' as m;

.paises{
    background-color: v.$verdemilitaroscuro;
    &__contenedor{
        @include m.contenedor;
        @include m.tabletl{
            display: flex;
        }
    }
    &__bloque{
        flex: 1;
        color: v.$blanco;
        padding: 4rem 0 6rem 0;
        &:nth-of-type(odd){
            background-color: v.$verdemilitaroscuro;
            .btn{
                a{
                    @include m.boton(v.$verde, v.$blanco)
                }
            }
        }
        &:nth-of-type(even){
            background-color: v.$verde;
            .paises__lista{
                li{
                    &::marker{  //viñeta de li
                        color: v.$verdemilitaroscuro;
                    }
                }
            }
            .btn{
                a{
                    @include m.boton(v.$verdemilitaroscuro, v.$blanco)
                }
            }
        }
    }
    &__contenido{
        width: 28rem;
        @include m.tabletl{
            width: 23rem;
        }
        @include m.tabletxl{
            width: 28rem;
        }
        margin: 0 auto;
    }
    &__icono{
        text-align: right;
        img{
            width: 5rem;
        }
    }
    &__descripcion{
        font-size: 1.2rem;
        font-weight: 300;
        margin: 1rem 0 3rem;
    }
    &__lista{
        list-style: disc;
        padding: 2rem;
        li{
            font-size: 1.3rem;
            &::marker{  //viñeta de li
                color: v.$verde;
                font-size: 2rem;
            }
        }
    }
}