@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    position: relative;
    min-height: 40rem;
    
    background-color: v.$verdemilitaroscuro;
    background-image: url(/build/img/PERRITO1.png), linear-gradient(270deg,rgba(68, 68, 68, 0.1),rgba(42, 42, 42, 0.295)), url(/build/img/circulos-semiblanco-izq.svg), url(/build/img/circulos-blancos-der.svg);
    background-repeat: no-repeat;
    
    background-size: contain, cover, 440px, 440px; 
    background-position: center bottom, center center, 50% 500%, left 130%;

    @include m.telefono{
        min-height: 50rem;
        background-size: contain, cover, 500px, 500px;
        background-position: right bottom, center center, -20% 250%, 140% 50%;
    }
    @media (width: 480px) {
        min-height: 50rem;
        background-size: contain, cover, 460px, 450px;
        background-position: right bottom, center center, -0% 250%, 100% 50%;
    }
    
    @include m.tablet{
        min-height: 60rem;
        background-size: contain, cover, 600px, 550px;
        background-position: right bottom, center center, -10% 330%, 112% -20%;
    }
    
    //background-position: top left, bottom right, center center, center center;
   
    &__contenedor{
        position: relative;
        @include m.contenedor;
        min-height: 40rem;
        @include m.telefono{
            min-height: 50rem;
        }
        @include m.tablet{
            min-height: 60rem;
        }
    }
    &__barranav{
        padding: 2rem 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        @include m.tabletxl{
            width: 50%;
        }
    }
    &__logo{
        width: 15rem;
    }
    &__nav{
        display: none;
        //gap: 1rem;
        a{
            font-size: 1.4rem;
            color: v.$blanco;
            border-left: 1px solid v.$gris;
            padding: .3rem 1rem;
            transition: all .5s ease;
            &:last-of-type{
                border-right: 1px solid v.$gris;
            }
            &:hover{
                color: v.$verde;
                //border-left: 1px solid v.$verde;
            }
        }
        @include m.tabletxl{
            display: flex;
        }
    }
    &__btnmenumovil{
        @include m.tabletxl{
            display: none;
        }
        .bars{
            color: v.$blanco;
            font-size: 3rem;
            cursor: pointer;
        }
    }

    &__contenidotext{
        text-align: center;
        @include m.telefono{
            text-align: left;
        }
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    &__titulo{
        @include m.texto_fluido(2.8rem, 5rem);
        @include m.telefono{
            text-align: left;
        }
        color: v.$blanco;
        span{
            color: v.$verde;
        }
    }
    &__descripcion{
        color: v.$grissemiclaro;
    }
    &__enlace{
        @include m.boton(v.$verde, v.$blanco);
        margin-top: 3rem;
    }

    &__menumovil{
        position: absolute;
        top: 0%;
        left: 0%;
        width: 0rem;
        height: 100%;
        background-color: rgba(v.$verde,.98);
        overflow: hidden;
        transition: all .6s ease;
    }
    .menumovil-js{
        width: 20rem;
    }
    &__navmovil{
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        gap: 1rem;
        padding: 0 0 0 3rem;
        .logomenumovil{
            width: 12rem;
            margin-bottom: 2rem;
        }
        a{
            font-size: 1.4rem;
            padding: 1rem 0;
            color: v.$grissemiclaro;
            position: relative;
            &::after{
                content: '';
                height: 1px;
                width: 0;
                background-color: v.$blanco;
                display: block;
                position: absolute;
                left: 50%;
                bottom: 0;
                transition: all .5s ease;
            }
            &:hover::after{
                    width: 100%;   
                    left: 0;
            }
            &:hover{
                color: v.$blanco;
            }
        }
    }
}