@use 'base/variables' as v;
@use 'base/mixins' as m;

.standpets{
    background-color: v.$verdelimaclaro;
    &__contenedor{
        @include m.contenedor;
        padding-top: 6rem;
        color: v.$verdemilitaroscuro;
        background-image: url(/build/img/huella.png);
        background-repeat: no-repeat;
        //background-size: contain;
        background-size: 120px;
        background-position: left top;
    }
    &__titulo{
        text-align: center;
        margin-bottom: 2rem;
    }
    &__imgtitulo{
        width: 20rem;
    }
    &__descripcion{
        text-align: center;
        width: auto;
        @include m.tablet{
            width: 50rem;
            margin: 0 auto;
        }
    }
    &__retangulo{
        background-color: v.$blanco;
        position: relative;
        text-align: center;
        border-radius: 1rem;
        max-width: 768px;
        margin: 0 auto;
        margin-top: 3rem;
        .perros{
            width: 12rem;
            @include m.tabletxl{
                position: absolute;
                &.perro2{
                    width: 18rem;
                    bottom: 91%;
                    right: 0;
                }
                &.perro3{
                    width: 26rem;
                    bottom: 0;
                    left: 0;
                    transform: translateX(-50%);
                }
            }
        }
        
        .huella{
            width: 12rem;
        }
    }
    &__textohuella{
        padding: 0 2rem;
        @include m.tabletxl{
            padding: 4rem 2rem;
        }
        @include m.tablet{
            display: flex;
            justify-content: flex-end;
            align-items: center;
            gap: 4rem;
            .texto{
                color: v.$verdemilitaroscuro;
                width: 47rem;
            }
        }
        .texto{
            font-size: 2.1rem;
            margin: 3rem 0;
        }
    }

}