@use 'base/variables' as v;
@use 'base/mixins' as m;

.petsanar{
    background-image: url(/build/img/logo_trasparente.svg), linear-gradient(270deg,v.$verde,v.$verde);
    background-repeat: no-repeat;
    background-size: contain, cover; 
    background-position: center right, center center;

    &__contenedor{
        @include m.contenedor;
        position: relative;

        min-height: 30rem;
    }
    &__texto{
        color: v.$blanco;
        text-align: center;
        padding: 3rem 0;
        @include m.tablet{
            display: flex;
            align-items: flex-end;
            width: 65%;
            margin: 0 auto;
        }
        @include m.tabletxl{
            margin-right: 0;
           margin-left: auto;
           padding: 4rem 0;
        }
        @include m.desktop{
            width: 72%;
        }
    }
    &__divp{

    }
    &__letrap{
        width: 13rem;
        min-width: 9rem;
    }
    &__parrafo{
        font-size: 1.7rem;
        padding: 0 2rem;
    }
    &__doctores{
        @include m.tablet{
            width: 45rem;
            
        }
        @include m.tabletxl{
            width: 50rem;
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }
}